@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --ivory: #FFFCEC;
  --charcoal: #313746;
  --fuzzy-wuzzy: #C97064;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #C97064;
  color: var(--fuzzy-wuzzy);
  background-color: #C97064;
  background-color: var(--fuzzy-wuzzy);
  font-family: Poppins, sans-serif;
}

.spinner,
.spinner:before,
.spinner:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation-fill-mode: both;
  animation: spinning 1.8s infinite ease-in-out;
}

.spinner {
  color: #FFFCEC;
  color: var(--ivory);
  font-size: 10px;
  margin: 80px auto;
  position: relative;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s;
}

.spinner:before,
.spinner:after {
  content: '';
  position: absolute;
  top: 0;
}

.spinner:before {
  left: -3.5em;
  animation-delay: -0.32s;
}

.spinner:after {
  left: 3.5em;
}

@keyframes spinning {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

.today-wrapper {
    display: flex;
}

.information-container {
    width: 50vw;
    height: 100vh;
    background-color: var(--ivory);
}

.today-container {
    display: flex;
    justify-content: center;
    flex-flow: column;
    padding-left: 50px;
}

.location-container {
    width: 100%;
    height: 20%;
}

.location-country {
    color: var(--charcoal);
    font-weight: 200;
}

.location-city {
    font-size: 24px;
    font-weight: 500;
}

.weather-container {
    display: flex;
    width: 100%;
    height: 60%;
}

.weather-container__left {
    width: 50%;
    height: 100%;
}

.weather-container__left p {
    margin: 15px 0;
}

.rain-chance::before {
    content: url(/ventus-weather/static/media/rain.d5135a6c.svg);
    margin-right: 10px;
}

.wind-speed::before {
    content: url(/ventus-weather/static/media/wind.70597e7d.svg);
    margin-right: 10px;
}

.clouds::before {
    content: url(/ventus-weather/static/media/clouds.974e56b3.svg);
    margin-right: 10px;
}

.humidity::before {
    content: url(/ventus-weather/static/media/humidity.6047f29f.svg);
    margin-right: 10px;
}

.weather-container__right {
    width: 50%;
    height: 100%;
}

.current-temperature {
    color: var(--charcoal);
    font-weight: 600;
    font-size: 42px;
}

.current-temperature::after {
    content: '\00b0';
    color: var(--fuzzy-wuzzy);
}

.feels-like {
    margin-bottom: 25px;
    font-size: 20px;
    font-weight: 200;
}

.feels-like::after {
    content: '\00b0';
    color: var(--charcoal);
}

.max-temperature::before {
    content: url(/ventus-weather/static/media/max-temperature.fb8755a3.svg);
    margin-right: 5px;
}

.min-temperature::before {
    content: url(/ventus-weather/static/media/min-temperature.5eb45943.svg);
    margin-right: 5px;
}

.max-temperature::after {
    content: '\00b0';
    color: var(--charcoal);
}

.min-temperature::after {
    content: '\00b0';
    color: var(--charcoal);
}

.temperature-container {
    display: flex;
    justify-content: center;
    flex-flow: column;
    width: 100%;
    height: 20%;
}

.temperature-trend {
    display: grid;
    grid-template-columns: repeat(5, 20%);
    padding-left: 50px;
}

.temperature-trend p {
    margin: 10px 0;
}

.temperature-trend:first-of-type {
    word-wrap: break-word;
}

.label {
    color: var(--charcoal);
}

.label ~ p::after {
    content: '\00b0';
    color: var(--charcoal);
}

.image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50vw;
    height: 100vh;
    background-color: var(--charcoal);
}

.image {
    width: 70%;
    height: auto;
}

@media screen and (max-width: 768px) {
    .today-wrapper {
        flex-flow: column-reverse;
    }

    .information-container {
        width: 100%;
        height: auto;
    }

    .location-container {
        margin: 50px 0;
    }

    .weather-container {
        flex-flow: column-reverse;
    }

    .weather-container__left, .weather-container__right {
        margin-bottom: 50px;
    }

    .temperature-trend {
        margin-bottom: 50px;
    }

    .label {
        grid-column-start: 1;
        grid-column-end: 6;
    }

    .image-container {
        width: 100%;
        height: auto;
    }    
}
.forecast-day-wrapper {
    justify-self: center;
    color: var(--ivory);
}

.weekday {
    font-size: 24px;
    font-weight: 500;
}

.date {
    margin: 5px 0 20px 0;
    color: var(--charcoal);
    font-size: 14px;
    font-weight: 200;
}

.value {
    font-size: 18px;
}

.temperature::before {
    content: url(/ventus-weather/static/media/thermometer.32530fa4.svg);
    margin-right: 10px;
}

.sunrise::before {
    content: url(/ventus-weather/static/media/sunrise.a5e6890d.svg);
    margin-right: 10px;
}

.sunset::before {
    content: url(/ventus-weather/static/media/sunset.fc428ab5.svg);
    margin-right: 10px;
}

@media screen and (max-width: 768px) {
    .forecast-day-wrapper {
        justify-self: stretch;
        margin-bottom: 100px;
    }
}
.seven-day-forecast-wrapper {
    display: grid;
    grid-template-columns: repeat(7, auto);
    margin: 100px 50px;
    color: var(--ivory);
}

@media screen and (max-width: 768px) {
    
    .seven-day-forecast-wrapper {
        grid-template-columns: repeat(1, auto);
        grid-template-rows: repeat(7, auto);
    }
}
